<template>
    <div class="all">
        <div class="banner"></div>
        <el-carousel :interval="4000" type="card" height="300">
            <el-carousel-item v-for="(item, index) in images" :key="item">
                <h3 class="medium"> <img :src="images[index]" alt="Image" width="100%" height="300px"></h3>
            </el-carousel-item>
        </el-carousel>
        <!-- <el-button @click="getData"></el-button> -->
        <div class='com-container'>
            <div class='com-chart' ref='achievement_ref'></div>
        </div>

    </div>
</template>
<script>
import { getHdiData } from '../../api/request.js';
export default {
    name: "AchienementComponents",
    data() {
        return {
            chartInstance: null,
            allData: null,
            images: [
                require('@/assets/images/achievement/A.png'),
                require('@/assets/images/achievement/B.png'),
                require('@/assets/images/achievement/C.png'),
                require('@/assets/images/achievement/D.png'),
                require('@/assets/images/achievement/E.png')
            ],
            hdiData: null,
            seletedYears: [],
            seletedArea: ["", "", "", "", '', ''],
        }
    },
    methods: {
        //echarts
        initChart() {
            this.chartInstance = this.$echarts.init(this.$refs.achievement_ref)
            const initOption = {
                title: {
                    text: '(HDI)',
                    left: '15%'
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false
                },
                yAxis: {
                    type: 'value',
                    scale: true,
                },
                tooltip: {
                    trigger: 'axis'
                },
                grid: {
                    top: '10%',
                    left: '10%',
                    right: '10%',
                    bottom: '5%',
                    containLabel: true
                },
            }
this.chartInstance.setOption(initOption)
        },
        async getData() {
            let year = this.seletedYears
            let columns = this.seletedArea
            getHdiData(year, columns).then(data => {
                this.hdiData = data
                this.updateChart()
            })


        },
        updateChart() {
            // 
            const years = this.hdiData.map(item => item.year)
            const chinaData = this.hdiData.map(item => item['']);
            const usaData = this.hdiData.map(item => item['']);
            const worldData = this.hdiData.map(item => item['']);
            const eData = this.hdiData.map(item => item[""]);
            const indiaData = this.hdiData.map(item => item[""]);
            const dataOption = {
                xAxis: {
                    data: years,
},
                series: [
                    {
                        name: '',
                        type: 'line',
                        data: chinaData,
                        smooth: true,
                        // showSymbol: false,

                          symbol: 'circle',   // symbol
                        symbolSize: 7,    //
                        lineStyle: {//
                            normal: {
                                color: 'red',
                                // smooth:true,
                                width: 6
                            }
                        },
                        itemStyle: {
                            color: 'yellow',
                            borderColor: "red", //  
                            borderWidth: 1, //  
                        }
                    },
                    {
                        name: '',
                        type: 'line',
                        data: usaData,
                        smooth: true,
                        // showSymbol: false,
                        // symbol: 'circle',   // symbol
                        symbolSize: 5,    //
                        lineStyle: {//
                            normal: {
                                color: 'blue',
                                // smooth:true,
                                // width:5
                            }
                        },
                        itemStyle: {
                            color: 'blue'
                        }
                    },
{
                        name: '',
                        type: 'line',
                        data: worldData,
                        smooth: true,
                        // showSymbol: false,
                        // symbol: 'circle',   // symbol
                        symbolSize: 5,    //
                        lineStyle: {//
                            normal: {
                                color: 'purple',
                                // smooth:true,
                                // width:5
                            }
                        },
                        itemStyle: {
                            color: 'purple'
                        }
                    },
                    {
                        name: "",
                        type: 'line',
                        data: eData,
                        smooth: true,
                        // showSymbol: false,
                        // symbol: 'circle',   // symbol
                        symbolSize: 5,    //
                        lineStyle: {//
                            normal: {
                                color: 'green',
                                // smooth:true,
                                // width:5
                            }
                        },
                        itemStyle: {
                            color: 'green'
                        }
                    },
{
                        name: "",
                        type: 'line',
                        data: indiaData,
                        smooth: true,
                        // showSymbol: false,
                        // symbol: 'circle',   // symbol
                        symbolSize: 5,    //
                        lineStyle: {//
                            normal: {
                                color: 'black',
                                // smooth:true,
                                // width:5
                            }
                        },
                        itemStyle: {
                            color: 'black'
                        }
                    },
                ],
                legend: {
                    data: ['', '', '', "", '']
                }
            }

            this.chartInstance.setOption(dataOption)
        },
        screenAdapter() {
            const adapterOption = {}
            this.chartInstance.setOption(adapterOption)
            this.chartInstance.resize()
        }

    },
    mounted() {
        this.initChart()
        this.getData()
        window.addEventListener('resize', this.screenAdapter)
        this.screenAdapter()
    },
    destroyed() {
        window.removeEventListener('resize', this.screenAdapter)

    }
}
</script>

<style lang="less" scoped>
.el-carouselitem h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
}

.el-carouselitem:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}

.com-container {
    height: 40vh;
}
.all{
    height: 93%;

}
</style>
